import { LitElement, html, css } from 'lit';
import { customElement, property } from 'lit/decorators.js';

/**
 * An example element.
 *
 * @slot - This element has a slot
 * @csspart button - The button
 */
@customElement('delta-text-button')
export class DeltaTextButton extends LitElement {
  static styles = css`
    :host {
      box-sizing: border-box;
      --delta-button-font-size: 1rem;
      --delta-button-font-weight: 600;
    }

    :host *,
    :host *::before,
    :host *::after {
      box-sizing: inherit;
    }
    button {
      background-color: #4caf50; /* Green */
      border: none;
      color: white;
      padding: 15px 25px;
      text-align: center;
      display: inline-block;
      font-size: var(--delta-button-font-size);
      font-weight: 600;
    }
  `;

  @property()
  label = '';

  render() {
    return html`
      <button
        type="button"
        @click="${() => {
          console.log('click');
        }}"
      >
        <slot></slot>
      </button>
    `;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'delta-text-button': DeltaTextButton;
  }
}
